body {
  margin: 0;
  background: #454545;
}
.board {
  width: 300px;
  height: 300px;
  margin: 30px auto;
}

.row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  height: 12.5%;
}

.square {
  -ms-flex-preferred-size: 12.5%;
      flex-basis: 12.5%;
  position: relative;
}

.piece {
  width: 80%;
  height: 80%;
  position: absolute;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  background-size: cover;
}

.light {
  background: #B38767;
}

.dark {
  background: #EED9B9;
}


